<script>
// @ is an alias to /src
    import Foro from '@/components/Foro.vue'
    import Logo from '@/components/Logo.vue'
    import Fotos from '../components/Fotos.vue'
    import Pie from '../components/PieDePagina.vue'
    import UsuarioCliente from '../components/UsuarioCliente.vue'
    import constantes from '@/constantes.js'

    export default {
        name: 'LogoVista',
        components: {
            Foro, 
            Logo,
            Fotos,
            Pie,
            UsuarioCliente
        },
        data() {
            return {
                usuarios: constantes.usuarios,
                checkedNames: [],
                nuevo: '',
                tipo: '',
                m: '',
                nuevaRespuesta: '',
                selected: [],
                allSelected: false,
                userIds: [],

                dialog: false,
                dialog2: false,
                dialog3: false,
                items: [
                    {
                        texto: "La clinica favorita de mi mejor amigo peludo :)",
                        nombre: "trabajador",
                        respuestas: [
                            {
                                us: "cliente",
                                contenido: "Muy buen trabajo"
                            }
                        ]
                    },
                    {
                        texto: "Siempre una experiencia genial, dejo a mi perro con toda seguridad y tranquilidad de que va a estar bien. Y cuando voy a recogerlo se ve que ha sido asi.",
                        nombre: "trabajador",
                        respuestas: [
                            {
                                us: "cliente",
                                contenido: "Muy buen trabajo"
                            }
                        ]
                    },
                    {
                        texto: "Se nota que tratan a mi perro super bien porque a pesar de que es un perro asustadizo está super contento y juguetos con ellos. Muy recomendable!",
                        nombre: "cliente",
                        respuestas: [
                            {
                                us: "cliente",
                                contenido: "Muy buen trabajo"
                            }
                        ]
                    },
                    {
                        texto: "Buenos y fiables profesionales con verdadero cariño por los animales. Notas que se preocupan de verdad por su salud y porque les estes cuidando bien.",
                        nombre: "cliente",
                        respuestas: [
                            {
                                us: "cliente",
                                contenido: "Muy buen trabajo"
                            }
                        ]
                    },
                    {
                        texto: "Han tratado a mi gata. Le han intentado salvar una pata pero posteriormente se tuvo que hacer una amputación. Son gente muy amable y tienen un horario muy amplio. Definitivamente es mi clinica favorita.",
                        nombre: "cliente",
                        respuestas: [
                            {
                                us: "cliente",
                                contenido: "Muy buen trabajo"
                            }
                        ]
                    },
                    {
                        texto: "Tarifas razonables.",
                        nombre: "trabajador",
                        respuestas: [
                            {
                                us: "cliente",
                                contenido: "Muy buen trabajo"
                            }
                        ]
                    },
                ]
            }
        },
    
        methods: {
            ayuda() {
                alert("Se encuentra en la sección del foro, aquí podrá abrir nuevas consultas escribiendo en el cuadro inferior.\n También si lo desea puede participar en consultas formuladas por otros usuarios presionando el botón de responder.\n Si considera que un comentario es inadecuado puede pulasr en eliminar para denunciarlo y nosotros procederemos a evaluarlo.")
            },
            volver() {
                return "/"
            },
            responderNo() {
                alert('Para escribir en el foro debe registrarse como usuario.')
            },
            responder(mensaje, tipo) {
                if (this.nuevaRespuesta == '') alert('¡Escribe tu respuesta!')
                else {
                    console.log(mensaje)
                    var i = 0;
                    var j = 0;
                    var t = false;
                    for (i = 0; i < this.items.length; i++) {
                        if (this.items[i].texto == mensaje) {
                            console.log(i)
                            var comentario = { us: tipo, contenido: this.nuevaRespuesta};
                            this.items[i].respuestas.push(comentario);
                        }

                    }
                    this.nuevaRespuesta = '';
                    this.dialog2 = false;
                }
               
            },
            eliminarClient() {
                
                if (this.checkedNames.length == 0) alert('Debes seleccionar los comentarios que quieras eliminar.')
                else {
                    var i = 0;
                    var j = 0; 
                    var t = false;
                    for (i = 0; i < this.checkedNames.length; i++) {
                        for (j = 0; j < this.items.length; j++) {
                            if (this.checkedNames[i] == this.items[j].texto) {
                                if(this.items[j].nombre == "trabajador") t = true
                                else this.items.splice(j, 1);
                            }
                        }
                    }
                    if (t) alert('No es posible eliminar comentarios de otros usuarios. Por favor seleccione solo comentarios escritos por su cuenta de usuario.')
                    
                }
            },
            eliminarComentarios(tipo) {
                
                if (tipo == "NoLogueado") alert('Debe registrarse para poder participar en el foro')
                else {
                    if (this.checkedNames.length == 0) alert('Debes seleccionar los comentarios que quieras eliminar.')
                    else {
                        var i = 0;
                        var j = 0;
                        for (i = 0; i < this.checkedNames.length; i++) {
                            for (j = 0; j < this.items.length; j++) {
                                if (this.checkedNames[i] == this.items[j].texto) this.items.splice(j, 1);
                            }
                        }   
                    }
                        
                    
                }
                    
            },
            enviar(tipo) {
                if (this.nuevo == '') alert('Debe escribir un comentario en el campo de texto.')
                else {
                    var comentario = { texto: this.nuevo, nombre: tipo };
                    this.items.push(comentario);
                    this.nuevo = '';
                    alert('¡Su comentario ha sido enviado con éxito!¡Gracias por colaborar en nuestro foro!');
                }
            },
            enviar2() {
                alert('Debe registrarse para poder participar en el foro.')

            },
            selectAll: function () {
                this.userIds = []; if (!this.allSelected) {
                    for (user in this.users) {
                        this.userIds.push(this.users[user].id);

                    }
                }
            },
            logeado() {
                if (this.log == "ERROR") {
                    return true
                }
                else {
                    return false
                }
            },
            cliente() {
                if (this.tipoCliente != "ERROR") {
                    console.log("Hola")
                    this.tipo = "cliente"
                    return true
                }
                else {
                    console.log(this.tipoCliente)
                    return false
                }
            }
            /*tipoTrabajador() {
                if (this.tipoTrabajador == "ERROR") {
                    return false
                }
                else {
                    return true
                }
            }*/
        },
        props: {
            tipoCliente: { type: String, default: "ERROR" },
            tipoTrabajador: { type: String, default: "ERROR" },
            log: { type: String, default: "ERROR" },
        }

    }
</script>

