
<template>
<div>
<h3 class="intro">Seleccione una imagen para subir a la galeria de fotos de la página</h3>

  <div class="drop" 
    :class="getClasses" 
    @dragover.prevent="dragOver" 
    @dragleave.prevent="dragLeave"
    @drop.prevent="drop($event)">

      <img :src="imageSource" v-if="imageSource" />
      <h1 v-if="wrongFile">Wrong file type</h1>
      <h1 v-if="!imageSource && !isDragging && !wrongFile">Arrastre una imagen</h1>

  </div>
  
   <div class="row">
     <v-col cols="11" align="center">
        
        <v-btn id="boton" @click.stop="dialog3 = true" class="boton" @click="imagen" href="" target="_blank" elevation="2">Subir imagen</v-btn>
        
         <v-dialog
            v-model="dialog3"
            max-width="500"
            >
            <v-alert type="success" > ¡Su imagen ha sido subida con éxito!</v-alert>
        </v-dialog>
         <v-btn id="boton" style="margin-left:20px;" @click.stop="dialog = true" class="boton" @click="imagen" href="" target="_blank" elevation="2">Cancelar</v-btn>
        
         <v-dialog
            v-model="dialog"
            max-width="500"
            >
            <v-alert type="success" >Cancelado</v-alert>
        </v-dialog>
    </v-col>
  </div>
  </div>
  
</template>



<script>
export default {
  name: 'DropAnImage',
  data(){
    return{
      isDragging:false,
      wrongFile:false,
      imageSource:null, 
      dialog3:false,
      dialog: false,
    }
  },
  computed:{
    getClasses(){
      return {isDragging: this.isDragging}
    }
  },
  methods:{
    dragOver(){
      this.isDragging = true
    },
    dragLeave(){
      this.isDragging = false
    },
    drop(e){
      let files = e.dataTransfer.files
      this.wrongFile = false
      // allows only 1 file
      if (files.length === 1) {
        let file = files[0]
        // allows image only
        if (file.type.indexOf('image/') >= 0) {
          var reader = new FileReader()
          reader.onload = f => {
            this.imageSource = f.target.result
            this.isDragging = false
          }
          reader.readAsDataURL(file)
        }else{
          this.wrongFile = true
          this.imageSource = null
          this.isDragging = false
        }
      }
      },
      imagen() {
          this.wrongFile = false
          this.isDragging = false
          this.imageSource = false
      },
    onRequestUploadFiles(){
      
    }
  }
}
</script>



<style scoped>
.drop{
  margin-left: 400px;
  margin-top:50px;
  width: 500px;
  height: 300px;
  background-color: #eee;
  border:10px solid #eee;
  
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  transition: background-color .2s ease-in-out;
  font-family: sans-serif;
}
.isDragging{
  background-color: #999;
  border-color: #fff;
}
img{
  width: 500px;
  height: 300px;
  object-fit: contain;
  
}

.intro{
margin-left:360px;
margin-top:50px;
}
</style>